body {
  margin: 0;
  padding: 0;
  font-family: 'Aleo', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh; }

* {
  box-sizing: border-box; }

code, button, input, optgroup, select, textarea {
  font-family: 'Aleo', serif; }

#root {
  height: 100%;
  width: 100%; }

.content {
  background: url(/static/media/background.39a11f15.jpg);
  background-position: center;
  background-size: cover;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.container {
  max-width: 1200px;
  margin: 0 auto;
  width: 90%; }

.nav__container {
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 625px;
  margin: 0 auto;
  padding: 0;
  padding-top: 40px;
  list-style: none;
  text-transform: uppercase; }
  @media screen and (max-width: 450px) {
    .nav__container {
      flex-wrap: wrap; } }

.nav__item {
  font-size: 13px;
  letter-spacing: 2px; }
  @media screen and (max-width: 450px) {
    .nav__item {
      order: 2;
      text-align: center;
      font-size: 12px; } }
  @media screen and (max-width: 450px) {
    .nav__item__img {
      width: 100%;
      order: 1;
      text-align: center;
      margin-bottom: 20px; } }
  .nav__item__link {
    cursor: pointer;
    color: #fff;
    padding: 10px 17px;
    text-decoration: none;
    -webkit-transition: 0.3s all;
    transition: 0.3s all;
    border-radius: 20px; }
    @media screen and (max-width: 530px) {
      .nav__item__link {
        padding: 10px 5px; } }
    .nav__item__link--hover:hover {
      background: #a83a5e; }

.jumbotron {
  text-align: center; }
  .jumbotron__images {
    margin-bottom: 50px; }
    @media screen and (max-width: 800px) {
      .jumbotron__images {
        max-width: 95%; } }
  .jumbotron__btn__content {
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 2px;
    padding: 20px 60px;
    border: 2px solid #f6f6f6;
    border-radius: 30px;
    cursor: pointer;
    -webkit-transition: 0.3s all;
    transition: 0.3s all;
    font-size: 13px; }
    @media screen and (max-width: 530px) {
      .jumbotron__btn__content {
        padding: 16px 40px; } }
    .jumbotron__btn__content--hover:hover {
      background: rgba(0, 0, 0, 0.43); }

.popup {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap; }
  .popup__overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.79);
    z-index: 2; }
  .popup__container {
    z-index: 3;
    width: 341px;
    height: 366px;
    padding: 45px 50px 47px;
    background: #fff;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center; }
    @media screen and (max-width: 370px) {
      .popup__container {
        padding: 40px 20px;
        max-width: 95%; } }
  .popup__headline {
    color: rgba(41, 45, 47, 0.46);
    padding-bottom: 40px;
    font-size: 18px;
    text-align: center;
    margin: 0; }
    .popup__headline--ok {
      padding-bottom: 0;
      color: #8bc34a; }
  .popup__input {
    width: 100%;
    text-align: center;
    margin: 0 0 32px;
    color: rgba(41, 45, 47, 0.57);
    font-size: 13px;
    line-height: 18px;
    padding: 12px 10px;
    border: 0;
    border-bottom: 1px solid #292d2f; }
    .popup__input--hidden {
      display: none; }
    .popup__input--error {
      border-bottom: 1px solid #a83a5e; }
  .popup__button {
    margin-top: 14px;
    font-weight: 700;
    width: 100%;
    border: 0;
    padding: 20px;
    background: #a83a5e;
    border-radius: 30px;
    color: #fff;
    letter-spacing: 2px;
    text-transform: uppercase;
    cursor: pointer;
    -webkit-transition: 0.3s all;
    transition: 0.3s all; }
    .popup__button:hover {
      background: #c35a7c; }

.footer {
  height: 49px;
  background: rgba(0, 0, 0, 0.43); }
  .footer__conteiner {
    height: 100%;
    display: flex;
    justify-content: space-between; }
    @media screen and (max-width: 530px) {
      .footer__conteiner {
        flex-direction: column;
        padding: 10px 0;
        align-items: center; } }
  .footer__col {
    display: flex;
    align-items: center; }
    @media screen and (max-width: 530px) {
      .footer__col {
        padding: 5px 0; } }
  .footer__p {
    font-size: 11px;
    margin: 0;
    color: #b7b7b7; }
    .footer__p span {
      cursor: pointer;
      -webkit-transition: 0.3s all;
      transition: 0.3s all; }
      .footer__p span:hover {
        color: #fff; }
    .footer__p strong {
      color: #fff; }
  @media screen and (max-width: 530px) {
    .footer {
      height: auto; } }

